import tabBar from '../../common/tabbar.vue';
import http from '../../common/http.js';
export default {
	data() {
		return {
			winHeight:0,
			scrollHeight: 0,
			adsHeight: 0,
			iconHeight: 0,
			searchViewHeight: 0,
			searchViewTop: 0,
			searchViewBottom: 0,
			firstNavTop: 0,
			secondNavLabelHeight: 0,
			iconSize: 0,
			keyword: '',
			isIphoneX: false,
			navList: [{
				firstNav: '开场助手',
				navId: 1,
				secondNav: [ 
					{ title: '重新开场', navClass:'second-nav-label second-nav-lable-margin',navId: 2, }, 
					{ title: '土味情话', navClass:'second-nav-label second-nav-lable-margin', navId: 3 }, 
					{ title: '表情话术', navClass:'second-nav-label', navId: 4 }, 
					{ title: '表情话术', navClass:'second-nav-label second-nav-lable-margin second-nav-lable-top', navId: 5 }, 
					{ title: '表情话术', navClass:'second-nav-label second-nav-lable-margin second-nav-lable-top', navId: 6 },
				]
			}]
		}
	},
	mounted() {
		this.isIphoneX = getApp().globalData.isIphoneX;
		this.getNavList();
		let url = '/';
		http.request(url, {}).then(resp=>{
			console.log('resp:');
		});
	},
	components:{
		tabBar
	},
	methods: {
		// 跳转到关键词页面
		searchKeyword() {
			uni.navigateTo({
				url: '/pages/index/huashu?keyword='+this.keyword
			});
		},
		getNavList() {
			
		},
		handleContact() {
			console.log('handler...');
		},
		search() {
			
		},
		upgrade_vip() {
			// 升级
			let userInfo = this.$store.getters.userInfo;
			console.log('userInfo', userInfo);
			if (userInfo.nickName === undefined) {
				uni.showModal({
					title: '提示',
					content: '请先登录',
					showCancel: false,
					cancelText: '',
					confirmText: '确定',
					success: res => {
						if(res.confirm) {
							console.log('res...confirm');
							uni.navigateTo({
								url: '/pages/user/index'
							});
						}
					},
					fail: () => {},
					complete: () => {
						
					}
				});
				return;
			}
			// 投诉建议
			uni.navigateTo({
				url: '/pages/user/upgrade_user_vip'
			});
		},
		complaint() {
			// 投诉建议
			uni.navigateTo({
				url: '/pages/user/complaint'
			});
		},
		help_center() {
			uni.navigateTo({
				url: '/pages/index/meme'
			});
		},
		contact_consumer() {
		
		},
		scroll() {
			
		},
		lower() {
			
		},
		upper() {
			
		},
		enter_huashu(title, navId) {
			uni.navigateTo({
				url:'/pages/index/huashu?title='+encodeURIComponent(title)+'&navId='+navId
			});
		}
	},
	onLoad() {
		let winHeight = uni.getSystemInfoSync().windowHeight;
		let ratio     = winHeight/731;
		ratio = ratio.toFixed(2);
		this.iconHeight = ratio *102; 
		this.adsHeight= ratio*175;
		this.winHeight = winHeight;
		this.searchViewHeight = 40* ratio;
		this.searchViewTop    = 20*ratio;
		this.searchViewBottom = 0;
		this.firstNavTop = 20*ratio;
		this.secondNavLabelHeight = 30*ratio;
		this.scrollHeight = winHeight - 175*ratio - 102*ratio - 80*ratio;
		this.iconSize = this.searchViewHeight - 10;
	}
}
